@font-face {
    font-family: 'transmission';
    src:  url('fonts/transmission.eot?ljv2x6');
    src:  url('fonts/transmission.eot?ljv2x6#iefix') format('embedded-opentype'),
    url('fonts/transmission.ttf?ljv2x6') format('truetype'),
    url('fonts/transmission.woff?ljv2x6') format('woff'),
    url('fonts/transmission.svg?ljv2x6#transmission') format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'transmission' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

